@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
:root {
  --word-sweep-duration: 400ms;
  --button-focus-color: rgb(189, 189, 189);
}

body {
  background-color: white;
  box-sizing: border-box;
}

* {
  font-family: 'Work Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
  sans-serif;
}

.App {
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Screen {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: white;
  min-height: 75vh;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 0;
  border-radius: 16px;
}

@media only screen and (max-width: 600px) {
  .Screen {
    min-height: 90vh;
  }
}

#title {
  font-size: 26px;
  font-weight: 600;
  text-align: center;
  color: rgb(73, 73, 73);
  cursor: default;
  margin-bottom: 20px;
}

.stylish-text {
  font-size: 18px;
  font-weight: 500;
  cursor: default;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: rgb(68, 68, 68);
  height: 40px;
  min-width: 225px;
  border-radius: 12px;
  padding: 5px 16px;
  border: solid 2px;
  border-color: rgb(226, 226, 226);
  background-color: rgb(245, 245, 245);
  margin: 0 10px;
  text-align: center;
}

.content {
  color: rgb(81, 107, 109);
  background-color: rgb(233, 240, 241);
  border-color: rgb(202, 213, 214);
  max-width: 300px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  font-size: 16px;
  padding: 6px 12px;
  text-align: left;
}

@media only screen and (max-width: 321px) {
  .content {
    font-size: 14px;
    padding: 3px 12px;
  }
}

ul {
  margin: 0;
}

li {
  margin: 12px 0px 12px -15px;
}

.stylish-button {
  display: block;
  height: 45px;
  min-width: 100px;
  font-weight: 500;
  color: rgb(116, 116, 116);
  font-size: 18px;
  background-color: rgb(255, 255, 255);
  border-radius: 12px;
  margin-bottom: 14px;
  padding: 4px 16px 5px 16px;
  border: solid 2px;
  border-bottom: solid 5px;
  border-color: rgb(226, 226, 226);
  background-color: white;
  box-shadow: inset rgb(0,0,0,0.045) 0px 1px 3px 1px, inset white 0px 0px 0px 3px;
}

.flex-row {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.ui-button:focus {
  outline: none;
}

@media only screen and (min-width: 600px) {
  .ui-button:focus, .ui-button:hover {
    border-color: rgb(189, 189, 189);
    border-color: var(--button-focus-color);
  }
}

.ui-button:active {
  outline: none;
  border-bottom-width: 2px;
  height: 40px;
  margin-top: 5px;
}

.move-into-view {
  animation: sweep-in 400ms cubic-bezier(0.33, 1, 0.68, 1);
  animation: sweep-in var(--word-sweep-duration) cubic-bezier(0.33, 1, 0.68, 1);
  animation-fill-mode: forwards;
}

.move-out-of-view {
  animation: sweep-out 400ms cubic-bezier(0.12, 0, 0.39, 0);
  animation: sweep-out var(--word-sweep-duration) cubic-bezier(0.12, 0, 0.39, 0);
  animation-fill-mode: forwards;
}

.sticky-footer {
  font-size: 12px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  bottom: 10px;
  left: 0;
  right: 0;
  text-align: center;
  background-color: white;
}

@keyframes sweep-in {
  from {
    transform: translateX(200px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
} 

@keyframes sweep-out {
  from {
    transform: translateX(0);
    opacity: 1;
  }
  to {
    transform: translateX(-200px);
    opacity: 0;
  }
}
:root {
  --scrollbarBG: rgba(211, 211, 211, 0);
  --thumbBG: #7c7c7c;
}

#end-word-list {
  padding: 5px 2px;
  max-height: 20vh;
  min-width: 200px;
  overflow-y: scroll;
  border: solid 2px lightgray;
  border-radius: 12px;
  scrollbar-width: thin;
  scrollbar-color: #7c7c7c rgba(211, 211, 211, 0);
  scrollbar-color: var(--thumbBG) var(--scrollbarBG);
}

#end-word-list::-webkit-scrollbar {
  width: 8px;
}

#end-word-list::-webkit-scrollbar-track {
  background: rgba(211, 211, 211, 0);
  background: var(--scrollbarBG);
}
#end-word-list::-webkit-scrollbar-thumb {
  background-color: #7c7c7c;
  background-color: var(--thumbBG) ;
  border-radius: 6px;
  border: 3px solid rgba(211, 211, 211, 0);
  border: 3px solid var(--scrollbarBG);
}

#end-word-list-title {
  text-align: center;
  margin-bottom: 6px;
  font-weight: 500;
}

.end-screen-word {
  text-align: center;
  color: gray;
  margin: 0;
}


:root {
  --gentle-red: rgb(206, 57, 57);
  --gentle-red-border: rgb(182, 49, 49);
  --gentle-green:  rgb(120, 200, 0);
  --gentle-green-border:  rgb(106, 177, 0);
  --word-shortcut-visibility: hidden;
}

@media only screen and (min-width: 600px) {
  .unselected-word:focus, .unselected-word:hover {
    border-color: var(--button-focus-color);
  }

  :root {
    --word-shortcut-visibility: visible;
  }
}

.word-button { 
  position: relative;
  background: linear-gradient(to left, rgb(255, 255, 255) 50%, rgb(120, 200, 0) 50%);
  background: linear-gradient(to left, rgb(255, 255, 255) 50%, var(--gentle-green) 50%);
  background-size: 200% 100%;
  background-position: right;
  opacity: 1;
}

.word-button:focus {
  outline: none;
}

.unselected-word:active {
  outline: none;
  border-bottom-width: 2px;
  height: 40px;
  margin-top: 5px;
}

.animate-word {
  color: white;
  background-position: left bottom;
  box-shadow: none;
  transition: all 350ms cubic-bezier(0.33, 1, 0.68, 1), box-shadow linear 330ms;
}

.correct-word {
  border-color: rgb(106, 177, 0);
  border-color: var(--gentle-green-border);
}

.correct-word:focus {
  border-color: rgb(106, 177, 0);
  border-color: var(--gentle-green-border);
}

.incorrect-word {
  background: none;
  background-color: rgb(206, 57, 57);
  background-color: var(--gentle-red);
  border-color: rgb(182, 49, 49);
  border-color: var(--gentle-red-border);
}

.incorrect-word:focus {
  border-color: rgb(182, 49, 49);
  border-color: var(--gentle-red-border);
}

.word-button[disabled] {
  color: rgb(190, 190, 190);
  border-color: rgb(236, 236, 236);
  box-shadow: inset white 0px 0px 0px 3px;
}

.word-button[disabled]:hover {
  border-color: rgb(236, 236, 236);
}

.word-shortcut-label {
  visibility: hidden;
  visibility: var(--word-shortcut-visibility);
  position: absolute;
  font-size: 12px;
  left: -85px;
  top: 11px;
  color: rgb(173, 173, 173);
}

.word-shortcut-key {
  visibility: hidden;
  visibility: var(--word-shortcut-visibility);
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  left: -30px;
  top: 10px;
  height: 14px;
  width: 14px;
  background-color: white;
  border: solid 2px  var(--button-focus-color);
  border-radius: 6px;
  color: gray;
}
#level-display {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  color: rgb(107, 107, 107);
  font-weight: 500;
  cursor: default;
}

.level-number-indicator {
  font-size: 18px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  height: 40px;
  min-width: 40px;
  border-radius: 12px;
  padding: 2px 8px;
  background-color: rgb(92, 189, 181); 
}
#lives-display {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
}

.life-icon {
  transition: color 150ms;
}
